import React, { useState } from 'react';
import { 
  Box, 
  Container, 
  Typography, 
  Card, 
  CardContent, 
  Button, 
  Paper,
  Chip,
  Alert,
  CircularProgress,
  IconButton,
  useMediaQuery,
  alpha,
  useTheme,
  Link,
  Grid,
  CardActions,
  CardHeader
} from '@mui/material';
import FolderIcon from '@mui/icons-material/FileCopy';
import UploadIcon from '@mui/icons-material/Upload';
import CloseIcon from '@mui/icons-material/Close';
import { fileUploadService } from '../services/fileUpload';
import { browserStorage } from '../services/browserStorage';
import GitHubIcon from '@mui/icons-material/GitHub';
import { trackAppEvent } from '../services/analytics';
import StarIcon from '@mui/icons-material/Star';
import SignUpForm from '../components/SignUpForm';

interface FileBrowserViewProps {
  onFilesSelected: (files: string[]) => void;
  selectedFiles: string[];
  setSelectedFiles: (files: string[]) => void;
  onGenerateGraph: () => void;
  onOpenServerSettings: () => void;
  selectedPreset: string | null;
  setSelectedPreset: (preset: string | null) => void;
  uploadedFiles: File[];
  setUploadedFiles: (files: File[]) => void;
}

interface DemoPreset {
  id: string;
  name: string;
  fileCount: number;
}

const demoPresets: DemoPreset[] = [
  {
    id: 'nestjs',
    name: 'NestJS example (TypeScript)',
    fileCount: 9,
  },
  {
    id: 'cs',
    name: 'Unity Game example (C#)',
    fileCount: 7,
  }
];

const FileBrowserViewV2 = ({ 
  onGenerateGraph, 
  selectedPreset,
  setSelectedPreset,
  uploadedFiles,
  setUploadedFiles
}: FileBrowserViewProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isUploading, setIsUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isDragging, setIsDragging] = useState(false);
  const [signUpDialogOpen, setSignUpDialogOpen] = useState(false);
  const [signUpType, setSignUpType] = useState<'personal' | 'team'>('personal');

  const handlePresetSelect = (presetId: string) => {
    setSelectedPreset(presetId);
    setError(null);
    trackAppEvent.demoPresetSelect(presetId);
  };

  const handleDragOver = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    setIsDragging(true);
  };

  const handleDragLeave = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    setIsDragging(false);
  };

  const handleDrop = async (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    setIsDragging(false);
    
    const files = Array.from(event.dataTransfer.files);
    if (files.length > 0) {
      await handleFileUpload(files);
    }
  };

  const handleFileUpload = async (files: File[]) => {
    setIsUploading(true);
    setError(null);
    
    try {
      // Upload files to server
      const result = await fileUploadService.uploadFiles(files);
      
      // Store uploaded file paths
      const newUploadedFiles = [...uploadedFiles, ...result.files];
      setUploadedFiles(newUploadedFiles);
      
      // Save to browser storage
      await browserStorage.set('uploadedFiles', newUploadedFiles);
      
      setSelectedPreset(null);
      console.log('Files uploaded successfully:', result.files);
      
      // Track successful file upload
      trackAppEvent.fileUpload(files.length);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to upload files');
      console.error('Upload error:', err);
      
      // Track upload error
      trackAppEvent.error('FileUpload', err instanceof Error ? err.message : 'Unknown error');
    } finally {
      setIsUploading(false);
    }
  };

  const handleFileInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || []);
    if (files.length > 0) {
      handleFileUpload(files);
    }
  };

  const handleRemoveFile = (indexToRemove: number) => {
    const newFiles = uploadedFiles.filter((_, index) => index !== indexToRemove);
    setUploadedFiles(newFiles);
    
    // Update selected files (assuming we need to maintain this for graph generation)
    const newUploadedFiles = uploadedFiles.filter((_, index) => index !== indexToRemove);
    setUploadedFiles(newUploadedFiles);
    
    // Update browser storage
    browserStorage.set('uploadedFiles', newUploadedFiles);
  };

  const handleClearAllFiles = () => {
    setUploadedFiles([]);
    browserStorage.set('uploadedFiles', []);
  };

  const handleSignUpClick = (type: 'personal' | 'team') => {
    setSignUpType(type);
    setSignUpDialogOpen(true);
  };

  const handleGenerateGraph = async () => {
    setIsLoading(true);
    setError(null);

    try {
      if (selectedPreset) {
        console.log('Loading demo preset:', selectedPreset);
        trackAppEvent.graphGenerate(selectedPreset);
        onGenerateGraph();
      } else if (uploadedFiles.length > 0) {
        console.log('Uploading files:', uploadedFiles);
        trackAppEvent.graphGenerate(null);
        onGenerateGraph();
      } else {
        setError('Please select a demo preset or upload files');
      }
    } catch (err) {
      setError('Failed to generate graph. Please try again.');
      console.error('Graph generation error:', err);
      trackAppEvent.error('GraphGeneration', err instanceof Error ? err.message : 'Unknown error');
    } finally {
      setIsLoading(false);
    }
  };

  const canGenerate = selectedPreset || uploadedFiles.length > 0;

  const theme = useTheme();
  const isSmUp = useMediaQuery(theme.breakpoints.up('md'));

  return (
    <Box sx={{ backgroundColor: 'background.default'}}>
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh'}}>
      <Box sx={{ 
      background: '#0e0e0e',
        backgroundImage: isSmUp ? 'url(cover11.png)' : 'none', 
        backgroundSize: 'cover', 
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat', 
        height: '40vh',
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        filter: 'brightness(0.8)',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.2)', // Dark overlay to reduce background prominence
          pointerEvents: 'none',
        }
      }}>
        {/* Page Title Content */}
        <Container maxWidth="lg" sx={{ display: 'flex', flexDirection: 'column', alignItems: isSmUp ? 'flex-start' : 'center', justifyContent: 'flex-start', textAlign: isSmUp ? 'left' : 'center' }}>
        <Box sx={{ position: 'relative', zIndex: 1 }}>
          <Typography variant='h1' component="h1" gutterBottom sx={{ 
            color: 'white', 
            fontWeight: 'bold', 
            textTransform: 'uppercase',
            textShadow: '2px 2px 4px rgba(0,0,0,0.8)',
            fontSize: '4rem'
          }}>
            codemaps
          </Typography>
          <Typography variant="subtitle1" sx={{ 
            color: 'white', 
            maxWidth: 400, 
            mx: 'auto', 
            mb: 2,
            textShadow: '1px 1px 2px rgba(0,0,0,0.8)',
            fontSize: '1.2rem',
          }}>
            A tool for visualizing all classes, methods, and how they relate to each other in your code
          </Typography>
          <Typography variant="subtitle2" sx={{ 
            color: 'primary.light', 
            maxWidth: 400, 
            mx: 'auto',
            textShadow: '1px 1px 2px rgba(0,0,0,0.8)',
            fontWeight: 'bold',
          }}>
            Supports TypeScript and C# codebases
          </Typography>
        </Box>
        </Container>
      </Box>
      <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
      
      <Container maxWidth="md" sx={{mt: 10, mb: 4}}>
      <Typography variant="h2" mb={3} sx={{ color: 'white', textAlign: 'center' }}>
          Improve your sense of ownership
        </Typography>
        <Typography variant="body1" mb={4} sx={{ color: 'white', textAlign: 'center' }}>
        Are you a <b>technical lead, architect or engineer</b> in charge of a software back-end ecosystem? 
        <br></br>
        Use visual perspective to gain better control over your codebase.
        </Typography>
        </Container>
        <Container maxWidth="md" sx={{justifyItems: 'flex-start' }}>
        <Card sx={{ backgroundColor: 'background.paper', width: 'fit-content', mb: 8}}>
        <CardContent sx={{px: 4}}>
        <Typography variant="h5" component="p" sx={{ color: 'white', mt: 1,mb: 2 }}>
          Makes it easier to
        </Typography>
        <Typography variant="body1" sx={{ color: 'white', ml: 2, lineHeight: 2, fontSize: '1.1rem' }}>
           <b>Communicate</b> decisions related to architecture and ownership<br />
           <b>Onboard</b> new team members<br />
           <b>Maintain</b> architectural hygiene<br />
           <b>Guide</b> AI agents<br />
        </Typography>
        </CardContent>
        </Card>
        </Container>
        <Box sx={{backgroundColor: '#0e0e0e'}}>
        <Container sx={{textAlign: 'center', p: 4, borderRadius: 2}}>
        <Typography variant="h3" sx={{ color: 'white', mb: 2 }}>
        Check out live demo!
        </Typography>
        <Typography variant="body1" sx={{ color: 'white' }}>
          There are many similar tools out there, each approching the same problem from a slightly different angle. 
          <br></br>
          Pick one of the demo options below to quickly see if this one's the right fit for you!
        </Typography>
      </Container>
      <Container>
        {/* Error Alert */}
        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}

        <Grid container spacing={4} gap={4}>
          {/* Demo Presets Section */}
          <Grid size={{ xs: 12, md: 6 }}>
            <Paper elevation={2} sx={{ p: 3, border: '2px solid', borderColor: selectedPreset ? 'primary.main' : 'transparent' }} variant='elevation'>
              <Typography variant="h5" component="h2" mb={3} sx={{ color: 'white' }}>
                Pick a demo file preset
              </Typography>

              <Box display="flex" flexDirection="column" gap={2}>
                {demoPresets.map((preset) => (
                  <Card 
                    key={preset.id}
                    sx={{ 
                      cursor: 'pointer',
                      borderColor: selectedPreset === preset.id ? 'primary.main' : 'divider',
                      backgroundColor: selectedPreset === preset.id ? 'primary.dark' : 'background.paper',
                      '&:hover': {
                        borderColor: 'primary.main',
                        boxShadow: 1
                      }
                    }}
                    onClick={() => handlePresetSelect(preset.id)}
                  >
                    <CardContent sx={{ pb: 1 }}>
                      <Typography variant="h6" component="h3" gutterBottom>
                        {preset.name}
                      </Typography>
                      <Box display="flex" alignItems="center" gap={1}>
                        <FolderIcon />
                        <Typography variant="caption" color="text.secondary">
                          {preset.fileCount} files
                        </Typography>
                      </Box>
                    </CardContent>
                  </Card>
                ))}
              </Box>
            </Paper>
          </Grid>

          {/* File Upload Section */}
          <Grid size={{ xs: 12, md: 6 }} display="flex">
            <Paper elevation={2} sx={{ flex: 1, p: 3, border: '2px solid', borderColor: !selectedPreset ? 'primary.main' : 'transparent' }} variant={'elevation'} onClick={() => handlePresetSelect('')}>
              <Typography variant="h5" component="h2" sx={{ color: 'white' }}>
                Try on your own code
              </Typography>
              <Typography variant="caption" color="grey.500">
                *Files uploaded in this demo experience will not be persisted for prolonged use of the app.
              </Typography>

              {/* File Drop Zone */}
              <Box 
                onDragOver={handleDragOver}
                onDragLeave={handleDragLeave}
                onDrop={handleDrop}
                sx={{
                  border: '2px dashed',
                  borderColor: isDragging ? 'success.main' : 'divider',
                  borderRadius: 2,
                  p: 4,
                  mt: 2,
                  textAlign: 'center',
                  backgroundColor: isDragging ? alpha(theme.palette.success.dark, 0.3) : !selectedPreset && uploadedFiles.length === 0 ? alpha(theme.palette.success.main, 0.15) : 'background.paper',
                  transition: 'all 0.2s ease-in-out',
                  cursor: 'pointer',
                  '&:hover': {
                    borderColor: 'success.main',
                    backgroundColor: 'background.dark'
                  }
                }}
              >
                <input
                  accept=".ts,.tsx,.js,.jsx,.cs,.java,.py,.go,.rs,.php,.rb"
                  style={{ display: 'none' }}
                  id="file-upload"
                  multiple
                  type="file"
                  onChange={handleFileInputChange}
                />
                <label htmlFor="file-upload">
                  <IconButton 
                    component="span" 
                    sx={{ mb: 2, fontSize: '2rem' }}
                    color={isDragging ? 'success' : 'default'}
                    disabled={isUploading}
                  >
                    {isUploading ? (
                      <CircularProgress size={32} color="inherit" />
                    ) : (
                      <UploadIcon sx={{ fontSize: '2rem' }} />
                    )}
                  </IconButton>
                </label>
                
                <Typography 
                  variant="body2" 
                  color={isDragging ? 'success' : 'text.secondary'} 
                  mb={2}
                  sx={{ fontWeight: isDragging ? 'bold' : 'normal' }}
                >
                  {isUploading 
                    ? 'Uploading files...' 
                    : isDragging 
                      ? 'Drop files here' 
                      : 'Drag & drop files here or click to select'
                  }
                </Typography>

                <Typography variant="body2" color="text.secondary">
                  Supports TypeScript and C# (.ts, .cs)
                </Typography>
              </Box>

              {/* Selected Files List */}
              {uploadedFiles.length > 0 && (
                <Box mt={3}>
                  <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                    <Typography variant="h6" component="h3" sx={{ color: 'white' }}>
                      Selected Files ({uploadedFiles.length})
                    </Typography>
                    <Button
                      size="small"
                      variant='outlined'
                      color="error"
                      onClick={handleClearAllFiles}
                      sx={{ fontSize: '0.75rem' }}
                    >
                      Clear All
                    </Button>
                  </Box>
                  <Box display="flex" flexDirection="column" gap={1}>
                    {uploadedFiles.map((file, index) => (
                      <Chip
                        key={index}
                        label={file.name}
                        size="small"
                        color='success'
                        variant={selectedPreset ? "outlined" : "outlined"}
                        onDelete={() => handleRemoveFile(index)}
                        deleteIcon={<CloseIcon />}
                        sx={{ alignSelf: 'flex-start' }}
                      />
                    ))}
                  </Box>
                </Box>
              )}
            </Paper>
          </Grid>

        <Grid size={{ xs: 12 }} textAlign="center">
          <Button
            variant="contained"
            size="large"
            onClick={handleGenerateGraph}
            disabled={!canGenerate || isLoading || isUploading}
            startIcon={isLoading || isUploading ? <CircularProgress size={20} color="inherit" /> : null}
            sx={{ 
              px: 6, 
              py: 2,
              fontSize: '1.2rem',
              minWidth: 250,
              backgroundColor: canGenerate ? 'primary.main' : 'grey.600',
              '&:hover': {
                backgroundColor: canGenerate ? 'primary.dark' : 'grey.600'
              }
            }}
          >
            {isUploading ? 'Uploading...' : isLoading ? 'Generating...' : 'View Graph'}
          </Button>
            <Typography variant="body2" color="white" mt={2} mb={4}>
              Select demo or upload files
            </Typography>
        </Grid>
        </Grid>
          {/* Action Section */}
        </Container>
        </Box>
      </Box>
    </Box>
    <Container sx={{mt: 10, mb: 12}}>
      <Typography variant="h4" color="text.secondary" align="center">Choose your plan</Typography>
      <Grid container spacing={4}>
        <Grid size={{ xs: 10, md: 5, lg: 4 }} offset={{ xs: 1, md: 1, lg: 2 }}>
          <Card sx={{mt: 2, height: '100%', display: 'flex', flexDirection: 'column'}}>
              <CardHeader sx={{backgroundColor: 'info.light', color:'info.text'}} title="Personal" subheader={<Typography variant="body1">
                <s>$15.90/year</s> <b style={{color: 'primary.main'}}>FREE (Limited Offer)</b>
              </Typography>}>
              </CardHeader>
            <CardContent sx={{py: 4, flex:1}}>
            <ul style={{ listStyleType: 'disc', paddingLeft: '20px', marginBottom: '20px', color: theme.palette.grey[400] }}>
              <li style={{ marginBottom: '10px' }}>The graph evolves with your codebase as you work</li>
              <li style={{ marginBottom: '10px' }}>Your files never leave your machine</li>
              <li style={{ marginBottom: '10px' }}>Easy setup with Docker <img src="/docker-mark-white.svg" alt="Docker" style={{ width: '24px', height: '24px', marginLeft: '6px', marginBottom: '-6px'}}/></li>
              <li style={{ marginBottom: '10px' }}>Integration with IDE (coming soon)</li>
            </ul>
            </CardContent>
            <CardActions sx={{justifyContent: 'center'}}>
            <Button onClick={() => handleSignUpClick('personal')} color="success">Claim access now</Button>
          
            </CardActions>
          </Card>
      </Grid>
      <Grid size={{ xs: 10, md: 5, lg: 4 }} offset={{ xs: 1, md: 0, lg: 0 }}>
      <Card sx={{mt: 2, height: '100%', display: 'flex', flexDirection: 'column'}}>
          <CardHeader sx={{backgroundColor: 'action.disabled', color:'action.disabled'}} title="Team" subheader={<Typography variant="body1" sx={{color: 'action.disabled'}}>
            $29.90 + $9.90 a month per seat
          </Typography>}>
            <Typography variant="h5" sx={{color: 'white'}}>
              Early Access
            </Typography>
            <Typography variant="body1" sx={{color: 'white'}}></Typography>
          </CardHeader>
        <CardContent sx={{py: 4, flex:1}}>
        <Typography variant="caption" sx={{color: 'white', textAlign: 'center'}}>All Personal features +</Typography>
        <ul style={{ listStyleType: 'disc', paddingLeft: '20px', marginBottom: '20px', color: theme.palette.grey[400] }}>
          <li style={{ marginBottom: '10px' }}>Collaboration mode (shared graph state)</li>
          <li style={{ marginBottom: '10px' }}>Customer support</li>
        </ul>
        </CardContent>
        <CardActions sx={{justifyContent: 'center'}}>
        <Button onClick={() => handleSignUpClick('team')} startIcon={<StarIcon />} color="warning">Join waitlist</Button>
      
        </CardActions>
      </Card>
      </Grid>
      </Grid>
    </Container>
    <Paper sx={{ py: 10 }}>
      <Container>
        <Box display="flex" flexDirection={{xs: 'column', md: 'row'}} alignItems="center" gap={2} justifyContent="space-between">
          <Box>
          <Typography variant="body1" color="text.secondary" >
            Codemaps 2025, All Rights Reserved
          </Typography>
          </Box>
          <Box display="flex" flexDirection="column" gap={2} alignItems={{xs: 'center', md: 'flex-start'}}>
          <Box display="flex" alignItems="center" gap={1}>
            <Link sx={{color: 'white'}} href="https://github.com/silverveskilt/code-maps-public" target="_blank" rel="noopener noreferrer" onClick={() => trackAppEvent.githubLinkClick()}>Read more on Github</Link>
            <GitHubIcon/>
          </Box>
          <Box>
            Created by <Link sx={{color: 'white'}} href="https://silverveskilt.com" target="_blank" rel="noopener noreferrer" onClick={() => trackAppEvent.authorLinkClick()}>silverveskilt.com</Link>
          </Box>
          </Box>
          </Box>
      </Container>
      </Paper>
      
      {/* Sign Up Dialog */}
      <SignUpForm
        open={signUpDialogOpen}
        onClose={() => setSignUpDialogOpen(false)}
        type={signUpType}
      />
    </Box>
  );
};

export default FileBrowserViewV2;
